/* 13. Appointment */
.appointment-section {
    background-size: cover;
    background-position: center top;
}

.appointment-content span {
    font-size: 18px;
    font-weight: 500;
    color: #e12454;
}

.appointment-box.white {
    background-color: #fff;
}

.appointment-content {
    padding-top: 55px;
    padding-left: 55px;
    padding-right: 55px;
}

.appointment-content h1 {
    font-size: 50px;
    margin-top: 35px;
    margin-bottom: 30px;
}

.appointment-box .primary_btn {
    width: 100%;
}

.professinals-list li {
    line-height: 28px;
    color: #697a8d;
    margin-bottom: 20px;
}

.professinals-list li i {
    width: 30px;
    height: 30px;
    line-height: 26px;
    text-align: center;
    border-radius: 50%;
    color: #8fb569;
    margin-right: 20px;
    float: left;
    border: 2px solid #8fb569;
    font-size: 12px;
    transition: .4s;
}

.professinals-list li:hover i {
    background: #8fb569;
    color: #fff;
}

/* 16. Appointment */
.appointment-box-2 {
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 16px 32px 0px rgba(223, 217, 255, 0.4);
    margin-top: -241px;
}

.appointment-box-content {
    padding: 70px 38px 40px 70px;
}

.appointment-form-3 textarea {
    width: 100%;
    resize: none;
    border: 2px solid #d3e2f0;
    padding-top: 15px;
    padding-left: 40px;
    padding-right: 40px;
    color: #647589;
    height: 80px;
}

.appointment-area-3 .calculate-box .nice-select {
    border: 2px solid #d3e2f0;
}

.appointment-area-3 .calculate-form input {
    border: 2px solid #d3e2f0;
}

.appointment-form-2 input {
    width: 100%;
}

.appointment-form-2 input {
    width: 100%;
    border: 0;
    padding: 0;
    padding-top: 5px;
}

.appointment-form-2 input::placeholder {
    font-size: 24px;
    color: #223645;
    font-weight: 600;
    font-family: poppins;
}

.appoint-ment-icon {
    margin-right: 40px;
}

.appointment-form-2 label {
    font-size: 12px;
    text-transform: uppercase;
    color: #aab6bf;
    font-weight: 500;
    margin-bottom: 14px;
}

.appointment-form-box {
    padding-bottom: 27px;
    border-bottom: 1px solid #eae5ff;
}

.appointment-form-box-option {
    padding-bottom: 8px;
}

.nice-select.postform::after {
    border-bottom: 3px solid #223645;
    border-right: 3px solid #223645;
    content: '';
    display: block;
    height: 8px;
    margin-top: -23px;
    pointer-events: none;
    position: absolute;
    right: 5px;
    top: 50%;
    -webkit-transform-origin: 66% 66%;
    -ms-transform-origin: 66% 66%;
    transform-origin: 66% 66%;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    -webkit-transition: all 0.15s ease-in-out;
    transition: all 0.15s ease-in-out;
    width: 8px;
}

.appointment_page_bg {
    background: url(../img/appointment/appointment-bg.jpg);
    background-repeat: no-repeat;
}

.calculate-content .select_style {
    border: 2px solid #d3e2f0;
}


.appointment-form-box form select {
    border: none;
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 15px;
    display: block;
    cursor: pointer;
    padding-right: 10px;

    & option {
        border: none;
        font-size: 18px;
        font-weight: 500;
        padding: 15px 0;
    }
}

.h4appointment-thumb {
    background: url(../img/home4/appointment/appointment__thumb.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}


.shtable {
    min-width: 1000px;

    .shtable__head {
        tr {
            th {
                border: none;
                // background: #13232f;
                background: transparent;
                color: white;
                padding: 1.5rem 0.5rem;
            }
        }
    }

    .shtable__body {
        tr {
            td {
                border: none;
                background: transparent;
                color: rgba(255, 255, 255, 0.3);
                padding: 1.5rem 0.5rem;
                font-size: 16px;
            }

            &:nth-child(odd) {
                background: rgba(0, 0, 0, 0.1);

            }

            &:hover {
                td {
                    color: white;
                }

                .highlight {
                    color: #8fb569;
                }
            }
        }


    }

}

.shtable-responsive {
    &::-webkit-scrollbar {
        height: 8px;
        width: 8px;
    }

    &::-webkit-scrollbar-track {
        /* box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); */
        background: rgb(243 246 255);
    }

    &::-webkit-scrollbar-thumb {
        background-color: gray;
    }
}

.norecord {
    background: rgba(0, 0, 0, 0.1);
    padding: 125px 50px;
    text-align: center;

    p {
        font-size: 50px !important;
        font-style: italic !important;
        color: rgba(255, 255, 255, 0.05);
    }

}

