/* Routine */
.routine__bg::before {
    position: absolute;
    content: "";
    background-color: rgba(34, 54, 69, 0.90);
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
}

.routine__table .table thead th,
.routine__table .table tbody th,
.routine__table .table tbody td {
    vertical-align: bottom;
    padding: 15px 10px;
    text-align: center;
    line-height: 1;
    font-size: 15px;
    color: #223645;
    font-weight: 700;
    background: #fff;
    border: 2px solid;
    font-family: 'Poppins', sans-serif;
    letter-spacing: -0.03em;
    min-width: auto;
}

.routine--time__day {
    margin-bottom: 5px;
}

.routine--time__date {
    font-weight: normal;
    font-size: 14px;
}

.doctor--routine__wrapper h2 {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 5px;
}

.doctor--routine__wrapper {
    div {
        font-size: 14px;
        font-weight: 500;
        color: #223645;
    }

}

.routine__table .table tbody td {
    padding: 25px 10px;
    font-size: 14px;
    background-color: rgba(21, 40, 55, 0.50);
}

.routine__table .table tbody td.active-doctor {
    background: #8fb569;
    cursor: pointer;
    transition: transform .2s;
    position: relative;

    /* Animation */
    i {
        position: absolute;
        display: none;
        top: -5px;
        right: -5px;
        background-color: #c80039;
        border-radius: 50%;
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    }

    &:hover {
        z-index: 100;
        transform: scale(1.2);
        /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
        background-color: #e12454;

        .doctor--routine__wrapper div {
            color: white;
        }
    }
}

.routine__table {
    .table {
        tbody {
            .clicked-cell {
                z-index: 100;
                transform: scale(1.2);
                /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
                box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
                background-color: #cf1c4a !important;

                i {
                    color: white;
                    display: inline-block !important;
                }

                .doctor--routine__wrapper div {
                    color: white;
                }
            }

            .booked-slot {
                pointer-events: none;

                i {
                    display: none !important;
                }

                .doctor--routine__wrapper div {
                    color: #647589;
                    rotate: -10deg;
                }
            }

        }
    }


}

.routine__bg {
    background-size: cover;
    background-position: center center;
}

.form-one {
    .form-heading {
        margin: 50px 0px 20px 0px;
    }

    .selected-date {
        margin-bottom: 15px;

        .label {
            font-size: 15px;
        }

        .success {
            color: #8fb569;
            margin-bottom: 0.5rem;
            display: inline-block;
        }

        .error {
            color: #c80039;
            margin-bottom: 0.5rem;
            display: inline-block;

        }
    }

    .filler {
        margin: 32px 0px;
    }
}